@import "@moai/core/dist/theme/module.scss";

.button {
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;

    text-align: left;
    cursor: pointer;

    /* ".secondary" doesn't have transition but Button.style.flat does so it
    causes laggy experience. Note that we don't need to keep the "outline"
    transition either because it's already "overflow: hidden" here */
    transition: none;
}

/* Remove Button.style.flat active style because it looks bad in double click
case. Should not disable the button because we still need dbl click to work */
.current:not(#weak):active {
    @include light {
        background-color: var(--light-gray-2);
    }
    @include dark {
        background-color: var(--dark-gray-2);
    }
}
