@import "@moai/core/dist/theme/module.scss";

.container {
    height: 100%;
    cursor: col-resize;
    width: 16px;
    margin-right: -15px;
    position: relative;
}

.line {
    height: 100%;
    transition: background-color 0.2s;

    .noDrag & {
        width: 1px;
        @include light {
            background-color: var(--light-gray-3);
        }
        @include dark {
            background-color: var(--dark-gray-3);
        }
    }

    .container:hover &,
    .drag & {
        width: 2px;
        @include light {
            background-color: var(--accent-2);
        }
        @include dark {
            background-color: var(--accent-2);
        }
    }
}

.disable {
    user-select: none;
}
